<template>
  <div class="dashboard">
    <vx-card class="mb-base">
      <div class="md:flex">
        <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-4">
          <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" :options="pilihanA" v-model="pilihanASelected" @input="updatePilihan"></v-select>
        </div>
        <div class="lg:w-1/3 mt-4 md:mt-0 md:ml-4">
          <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" label="nama" :options="pilihanB" v-model="pilihanBSelected"  ></v-select>
        </div>
        <div class="lg:w-1/4 mt-4 md:mt-0 md:ml-4">
          <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Awal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedStart" ></datepicker>
        </div>
        <div class="lg:w-1/4 mt-4 md:mt-0 md:ml-4">
          <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Akhir --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedEnd" ></datepicker>
        </div>
        <!-- <div class="md:mt-0 md:mx-6">
          <vcheckboxpopup :label="'Entitas'" v-model="selected" :arrayList="entitas" style="display:inline;"></vcheckboxpopup>
        </div> -->
        <div class="mt-4 md:mt-0 md:ml-4">
          <div class="flex">
            <div class="w-12">
              <vs-button :disabled="!validateForm" radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline; margin-right:30px;"></vs-button>
            </div>
            <div class="w-12 ml-2">
              <vs-button :disabled="cardShow == false"  @click="copyImage" color="primary" type="border" icon-pack="feather" icon="icon-copy"></vs-button>
              <!-- <vs-button @click="copyImage" color="primary" type="border" icon-pack="feather" icon="icon-copy"></vs-button> -->
            </div>
          </div>
        </div>   
      </div>
    </vx-card>

    <div v-show="cardShow == true">
    <!-- <div> -->
      <div ref="printcontent">
        <br v-show="themeCopyShow == true">
        <div :class="themeCopyShow == true ? 'highlight mx-3' : ''">
          <div v-show="themeCopyShow == true" class="w-full">
            <h3 class="pl-5">{{ themeCopy.title }}</h3>
          </div>

          <hr class="mb-0" v-show="themeCopyShow == true" style="border-bottom: 1px #7367f0 solid;">

          <div v-show="themeCopyShow == true" class="w-full">
            <h5 class="pl-5">Periode: {{ themeCopy.periode }}</h5>
          </div>

          <hr class="mt-0" v-show="themeCopyShow == true" style="border-bottom: 1px #7367f0 solid;">

          <div class="vx-row">
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Store'" :icon="'storefront'" :value="store"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'NoT'" :icon="'receipt'" :value="noT"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Sales Total'" :icon="'shopping_cart'" :value="salesTotal"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Avg NoT / Day'" :icon="'timeline'" :value="averageNoTDay"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Avg Sales / Day'" :icon="'timeline'" :value="averageSalesDay"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Avg Sales / Transaction'" :icon="'timeline'" :value="averageSalesTransaction"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Avg NoT / Store / Day'" :icon="'timeline'" :value="averageNoTStoreDay"></dashboardCard>
            <dashboardCard class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col mb-base" :label="'Avg Sales / Store / Day'" :icon="'timeline'" :value="averageSalesStore"></dashboardCard>
          </div>

          <hr v-show="themeCopyShow == true" style="border-bottom: 1px  #7367f0  dashed;">

          <div :class="themeCopyShow == true ? 'vx-row pt-8' : 'vx-row pt-8 highlight' ">
            <div class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col">
              <!-- <dashboardCard class="mb-base" :label="'Total Pelanggan'" :icon="'person'" :value="totalPelanggan"></dashboardCard> -->
              <vx-card class="mb-base">
                <div class="text-center my-4">
                  <div style="font-size: 38px;" class="mb-0 pt-2 text-black font-semibold">{{totalPelanggan}}</div>
                  <p class="text-grey mb-0">Total Pelanggan</p>
                </div>
              </vx-card>
              <!-- <dashboardCard class="mb-base" :label="'Jumlah Pesanan'" :icon="'assignment'" :value="jmlPesanan"></dashboardCard> -->
              <vx-card class="mb-base">
                <div class="text-center my-4">
                  <div style="font-size: 38px;" class="mb-0 pt-2 text-black font-semibold">{{jmlPesanan}}</div>
                  <p class="text-grey mb-0">Jumlah Pesanan</p>
                </div>
              </vx-card>
            </div>
            <div class="w-full sm:w-1/2 md:w-1/3 lg:w-1/4 vx-col">
              <!-- <dashboardCard class="mb-base" :label="'Total Pesanan'" :icon="'assignment'" :value="totalPesanan"></dashboardCard> -->
              <vx-card class="mb-base">
                <div class="text-center my-5">
                  <div style="font-size: 30px;" class="mb-2 pt-2 text-black font-semibold">{{totalPesanan}}</div>
                  <p class="text-grey mb-0">Total Pesanan</p>
                </div>
              </vx-card>
              <vx-card class="mb-base">
                <div class="text-center my-1">
                  <div style="font-size: 36px;" class="mb-0 pt-2 text-black font-semibold">{{kontribusiPesanan}}</div>
                  <p class="text-grey mb-0">Kontribusi Pesanan <br> Terhadap Sales</p>
                </div>
              </vx-card>
            </div>
            <div class="w-full lg:w-1/2 vx-col">
              <vx-card class="mb-base"> 
                  <h6 class="text-center">Komposisi Jenis Pesanan</h6>
                  <apexchart class="my-4 mx-auto" height="230" ref="updateChartPelanggan" type="pie" :options="chartOptionsPelanggan" :series="seriesPelanggan"></apexchart>
              </vx-card>
            </div>
          </div>

          <hr v-show="themeCopyShow == true" class="mb-0" style="border-bottom: 1px #7367f0 solid;">

          <div v-show="themeCopyShow == true" class="flex">
            <div class="w-1/2">
              <h6 v-show="themeCopyShow == true" class="pl-5">{{ themeCopy.reqDate }}</h6>
            </div>
            <div class="w-1/2 text-right">
              <h6 v-show="themeCopyShow == true" class="pr-5">{{ themeCopy.copyDate }}</h6>
            </div>
            <!-- <h3 v-show="themeCopyShow == true">SEMUA TOKO Bandung Selatan 3 + Utara</h3> -->
          </div>
        </div>
        <br v-show="themeCopyShow == true">
      </div>
  
      <!-- <div class="vx-row">
        <div class="w-full lg:w-1/4 vx-col">
          <dashboardCard class="mb-base" :label="'Cost'" :icon="'account_balance_wallet'" :value="cost"></dashboardCard>
          <dashboardCard class="mb-base" :label="'Crew Store'" :icon="'people'" :value="jumlah"></dashboardCard>
          <dashboardCard class="mb-base" :label="'Productivity'" :icon="'format_line_spacing'" :value="produktivitas"></dashboardCard>
        </div>
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card >
            <apexchart ref="updateChartSession" type="bar" height="359" :options="chartOptionsSession" :series="seriesSession"></apexchart>
          </vx-card>
        </div>
      </div> -->
  
      <vx-card class="mb-base mt-8">
        <apexchart ref="updateCharthourlySales" type="area" height="350" :options="chartOptionshourlySales" :series="serieshourlySales"></apexchart>
      </vx-card>
  
      <vx-card class="mb-base">
        <apexchart ref="updateCharthourlyVoid" type="area" height="350" :options="chartOptionshourlyVoid" :series="serieshourlyVoid"></apexchart>
      </vx-card>
  
      <vx-card class="mb-base">
        <apexchart ref="updateChartdateVoid" type="area" height="350" :options="chartOptionsdateVoid" :series="seriesdateVoid"></apexchart>
      </vx-card>
  
      <vx-card class="mb-base">
        <apexchart ref="updateChart" type="line" height="350" :options="chartOptions" :series="series"></apexchart>
      </vx-card>
      
      <div class="flex vx-row">
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card >
            <apexchart ref="updateChartSession" type="bar" height="359" :options="chartOptionsSession" :series="seriesSession"></apexchart>
          </vx-card>
        </div>
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card >
            <div class="table-wrapper-scroll-y my-custom-scrollbar-sales">
              <vs-table
                :data="salesmenu"
                search>
                <template slot="header">
                  <vx-tooltip text="Export Excel" style="display:inline">   
                    <vs-button @click="tableToExcel('table', 'Data Sales')" color="success" type="filled" style="display:inline;" size="small"><i class="material-icons">description</i></vs-button>    
                  </vx-tooltip>
                </template>
  
                <template slot="thead" >
                  <vs-th sort-key="menu_toko" class="bg-primary text-white">
                    MENU TOKO
                  </vs-th>
                  <vs-th sort-key="qty" class="bg-primary text-white text-right">
                    QTY
                  </vs-th>
                  <vs-th sort-key="rp" class="bg-primary text-white text-center" >
                    RP
                  </vs-th>
                  <vs-th sort-key="kontribusi_sales" class="bg-primary text-white text-center">
                    KONTRIBUSI SALES
                  </vs-th>
                </template>
  
                <template slot-scope="{data}">
                  <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" >
                    <vs-td :data="data[indextr].menu_toko">
                      {{data[indextr].menu_toko}}
                    </vs-td>
  
                    <vs-td :data="data[indextr].qty" class="text-right">
                      {{data[indextr].qty.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
                    </vs-td>
  
                    <vs-td :data="data[indextr].rp" class="text-right">
                      {{data[indextr].rp.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
                    </vs-td>
  
                    <vs-td :data="data[indextr].kontribusi_sales" class="text-right">
                      {{data[indextr].kontribusi_sales}}%
                    </vs-td>
                  </vs-tr>
                </template>
              </vs-table>
            </div>
          </vx-card>
  
            <!-- Cetak Excel -->
            <table hidden ref="table" id="loremTable" summary="lorem ipsum sit amet" rules="groups" frame="hsides" >
              <thead>
                  <tr>
                      <th colspan="4" style="text-align:left"><h4>{{judul}}</h4></th>
                  </tr>
                  <tr>
                      <th></th>
                  </tr>
                  <tr>
                      <th></th>
                  </tr>
                  <tr>
                      <th>Menu Toko</th>
                      <th>Qty</th>
                      <th>Rp</th>
                      <th>Kontribusi Sales</th>
                   </tr>
              </thead>
  
              <tbody>
                  <tr :key="items.id" v-for="items in salesmenu">
                      <td>{{items.menu_toko}}</td>
                      <td style="text-align:right">{{items.qty}}</td>
                      <td style="text-align:right">{{items.rp}}</td>
                      <td style="text-align:right">{{items.kontribusi_sales}}%</td>
                  </tr>
              </tbody>
            </table>
        </div>
      </div>
  
      <vx-card>
        <div class="w-full bg-primary p-1 text-center">
          <h3 class="text-white">Tracking Omzet</h3>
        </div>
        <div class="lg:w-1/3 mt-5 ml-2">
          <h6><strong>BULAN :</strong> {{bulan}}</h6>
        </div>
        <div class="lg:w-1/3 mt-5 ml-2">
          <h6><strong>GERAI :</strong> {{namagerai}}</h6>
        </div>
        <div class="lg:w-1/3 mt-5 ml-2 mb-3">
          <h6><strong>TARGET GERAI :</strong> {{targetgerai}}</h6>
        </div>
   
        <div class="table-wrapper-scroll-y my-custom-scrollbar">
        <vs-table 
          :data="omzet">
          <template slot="thead" >
            <vs-th sort-key="hari" class="bg-primary text-white">
              DAY
            </vs-th>
            <vs-th sort-key="tgl" class="bg-primary text-white">
              DATE
            </vs-th>
            <vs-th sort-key="notLMTD" class="bg-primary text-white">
              NOT LM
            </vs-th>
            <vs-th sort-key="notMTD" class="bg-primary text-white">
              NOT NOW
            </vs-th>
            <vs-th sort-key="actualLMTD" class="bg-primary text-white">
              SALES LM
            </vs-th>
            <vs-th sort-key="actualMTD" class="bg-primary text-white">
              SALES NOW
            </vs-th>
            <vs-th sort-key="cost" class="bg-primary text-white">
              COST
            </vs-th>
            <vs-th sort-key="gerai" class="bg-primary text-white">
              STORE
            </vs-th>
            <vs-th sort-key="avgstr" class="bg-primary text-white">
              AVG/STR
            </vs-th>
          </template>
  
          <template slot-scope="{data}">
            <vs-tr  :state="data[indextr].hari == 'Sabtu' || data[indextr].hari  == 'Minggu' ? 'primary':null" :data="tr" :key="indextr" v-for="(tr, indextr) in data" >
              <vs-td :data="data[indextr].hari">
                {{data[indextr].hari}}
              </vs-td>
  
              <vs-td   :data="data[indextr].tgl">
                {{data[indextr].tgl}}
              </vs-td>
  
              <vs-td  :data="data[indextr].notLMTD" >
                {{data[indextr].notLMTD.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
              </vs-td>
  
              <vs-td  :data="data[indextr].notMTD" >
                {{data[indextr].notMTD.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
              </vs-td>
  
              <vs-td  :data="data[indextr].actualLMTD" >
                {{data[indextr].actualLMTD.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
              </vs-td>
  
              <vs-td  :data="data[indextr].actualMTD" >
                {{data[indextr].actualMTD.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
              </vs-td>
  
               <vs-td  :data="data[indextr].cost" >
                {{data[indextr].cost.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
              </vs-td>
  
              <vs-td  :data="data[indextr].gerai" >
                {{data[indextr].gerai}}
              </vs-td>
  
              <vs-td :data="data[indextr].avgstr" >
                {{data[indextr].avgstr.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
              </vs-td>
            </vs-tr>
            <!-- <vs-tr>
              <vs-td class="bg-primary text-white text-right">
                RATA - RATA
              </vs-td>
              <vs-td class="bg-primary text-white">
              30
              </vs-td>
            </vs-tr>
            <vs-tr>
              <vs-td class="bg-primary text-white text-right">
                X
              </vs-td>
              <vs-td class="bg-primary text-white">
              
              </vs-td>
              <vs-td class="bg-primary text-white">
              310900909
              </vs-td>
              <vs-td class="bg-primary text-white">
              879878989
              </vs-td>
              <vs-td class="bg-primary text-white">
              989989989
              </vs-td>
              <vs-td class="bg-primary text-white">
              09899890989
              </vs-td>
              <vs-td class="bg-primary text-white">
              98%
              </vs-td>
              <vs-td class="bg-primary text-white">
              
              </vs-td>
              <vs-td class="bg-primary text-white">
              098909890
              </vs-td>
            </vs-tr>
            <vs-tr>
              <vs-td class="bg-primary text-white text-right">
                RATA2
              </vs-td>
              <vs-td class="bg-primary text-white">
              
              </vs-td>
              <vs-td class="bg-primary text-white">
              310900909
              </vs-td>
              <vs-td class="bg-primary text-white">
              879878989
              </vs-td>
              <vs-td class="bg-primary text-white">
              989989989
              </vs-td>
              <vs-td class="bg-primary text-white">
              09899890989
              </vs-td>
              <vs-td class="bg-primary text-white">
              98%
              </vs-td>
              <vs-td class="bg-primary text-white">
              
              </vs-td>
              <vs-td class="bg-primary text-white">
              098909890
              </vs-td>
            </vs-tr> -->
          </template>
        </vs-table>
        </div>
      </vx-card>
    </div>

  </div>
</template>


<script>
import VueApexCharts from 'vue-apexcharts'
import Datepicker from 'vuejs-datepicker'
import dashboardCard from './DashboardCard'
import html2canvas from 'html2canvas'
import { copyImageToClipboard } from 'copy-image-clipboard'

export default {
   data:function(){
     return{
        cardShow: false,
        themeCopyShow: false,
        themeCopy: {
          title: '',
          periode: '',
          reqDate: '',
          copyDate: ''
        },
        uri :'data:application/vnd.ms-excel;base64,',
        template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
        base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
        format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
        judul:'',
        // Board
        store:null, 
        noT:null,
        salesTotal:null,
        averageNoTDay:null,
        averageSalesDay:null,
        averageSalesTransaction:null,
        averageNoTStoreDay:null,
        averageSalesStore:null,
        cost:null,
        jumlah:null,
        produktivitas:null,
        // Board2
        kontribusiPesanan:null,
        jmlPesanan:null,
        totalPelanggan:null,
        totalPesanan:null,
        // Select Option 1
        pilihanA:['Region','Wilayah','Area','Cabang','Toko'],
        pilihanASelected:null,
        // Select Option 2
        pilihanB:[],
        pilihanBSelected:null,
        // Select 3
        selectedStart:null,
        // Select 4
        selectedEnd:null,
        // Selct Option 5
        selectedMonth:new Date(),
        // Head Table Omzet
        bulan:null,
        namagerai:null,
        targetgerai:null,
        // Isi Table Omzet
        omzet:[],
        // Isi Table Void
        voids:[],
        // Grafik Chart Bar Line 
        series:[],
        chartOptions: {
          plotOptions: {
            bar: {
              horizontal: false,
              dataLabels: {
                position: 'top',
              }
            }
          },
          chart: {
              height: 350,
              type: 'line',
          },
          tooltip: {
            y: {
              formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }
            }
          },
          fill: {
              colors: [function({ value, w ,dataPointIndex}) {
                if(parseFloat(parseFloat(value)/parseFloat(w.globals.series[0][dataPointIndex])*100) >= 100) {
                  return '#008efb'
                } else {
                  return '#FFBE46'
                }
              }]
          },
          stroke: {
              width: [0, 5],
              curve: 'smooth'
          },
          title: {
              text: '',
              align:'center'
          },
          colors: ['#008efb','#FFBE46'],
          dataLabels: {
              enabled: true,
              enabledOnSeries: [0],
              formatter: function (val) {
                return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              },
          },
          labels: [],
          xaxis: {
              type:'string',
          },
          yaxis: [{
            min:0
          }]
        },
        // Grafik Chart Bar Session
        seriesSession:[],
        chartOptionsSession: {
          chart: {
            height: 350,
            type: 'bar',
          },
          title: {
            text: '',
            align:'center'
          },
          colors: ['#008efb'],
          dataLabels: {
            enabled: true,
            enabledOnSeries: undefined,
            textAnchor: 'middle',
            background: {
              enabled: true,
              foreColor: '#000',
              padding: 4,
              borderRadius: 2,
              borderWidth: 1,
              borderColor: '#fff',
              opacity: 0.9,
            },
            formatter: function (val) {
              return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            },
          },
          tooltip: {
            y: {
              formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }
            }
          },
          labels: [],
          xaxis: {
            type:'string',
          },
          yaxis: [{
            title: {
            text: 'Omset'
          }
          }]
        },
        // Grafik Sales
        serieshourlySales:[],
        chartOptionshourlySales: {
          chart: {
            height: 350,
            type: 'line',
          },
          stroke: {
            curve: 'smooth',
            width: [1, 1]
          },
          fill: {
            type:'solid',
            opacity: [1, 1],
          },
          colors: ['#008efb','#EA5455'],
          markers: {
            size: 0
          },
          dataLabels: {
            enabled: true,
            formatter: function (val) {
              return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            },
            // offsetX: -10,  
          },
          yaxis: [
            {
              title: {
                text: 'SALES',
              },
            },
            {
              opposite: true,
              title: {
                text: 'NOT',
              },
            },
          ],
          grid : {
            padding: {
              top: 0,
              right: 30,
              bottom: 0,
              left: 30
            }
          },
          tooltip: {
            shared: true,
            intersect: false,
              y: {
              formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }
            }
          },
        },
        // Grafik Hourly Void
        serieshourlyVoid:[],
        chartOptionshourlyVoid: {
          chart: {
            height: 350,
            type: 'line',
          },
          stroke: {
            curve: 'smooth',
            width: [1, 1]
          },
          fill: {
            type:'solid',
            opacity: [1, 1],
          },
          colors: ['#008efb','#EA5455'],
          markers: {
            size: 0
          },
          dataLabels: {
            enabled: true,
            formatter: function (val) {
              return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            },
          },
          yaxis: [
            {
              title: {
                text: 'VOID',
              },
            },
            {
              opposite: true,
              title: {
                text: 'NOT',
              },
            },
          ],
          grid : {
            padding: {
              top: 0,
              right: 30,
              bottom: 0,
              left: 30
            }
          },
          tooltip: {
            shared: true,
            intersect: false,
              y: {
              formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }
            }
          },
        },
        // Grafik Date Void
        seriesdateVoid:[],
        chartOptionsdateVoid: {
          chart: {
            height: 350,
            type: 'line',
          },
          stroke: {
            curve: 'smooth',
            width: [1, 1]
          },
          fill: {
            type:'solid',
            opacity: [1, 1],
          },
          colors: ['#008efb','#EA5455'],
          markers: {
            size: 0
          },
          dataLabels: {
            enabled: true,
            formatter: function (val) {
              return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            },
          },
          yaxis: [
            {
              title: {
                text: 'VOID',
              },
            },
            {
              opposite: true,
              title: {
                text: 'NOT',
              },
            },
          ],
          grid : {
            padding: {
              top: 0,
              right: 30,
              bottom: 0,
              left: 30
            }
          },
          tooltip: {
            shared: true,
            intersect: false,
              y: {
              formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }
            }
          },
        },
        // Isi Table Sales Menu
        salesmenu:[],
        // Grafik Pelanggan
        seriesPelanggan: [],
        chartOptionsPelanggan: {
          chart: {
            width: 380,
            type: 'pie',
          },
          responsive: [{
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: 'bottom'
              }
            }
          }],
          tooltip: {
            y: {
              formatter: function(value) {
                return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
              }
            }
          },
        }
     }
    },
    
  components: {
    apexchart: VueApexCharts,
    Datepicker,
    dashboardCard,
    html2canvas
  },
  
  computed: {
    validateForm() {
      return !this.errors.any() && this.pilihanBSelected != null && this.selectedStart != null && this.selectedEnd != null;
    },
  },

  methods: {
    tableToExcel(table, name) {
        if (!table.nodeType) table = this.$refs.table
        var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
        window.location.href = this.uri + this.base64(this.format(this.template, ctx))
    },

    copyImage() {
      this.$vs.loading();
      this.themeCopyShow = true;

      // DateNow CopyDate
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      const hours = String(now.getHours()).padStart(2, '0');
      const minutes = String(now.getMinutes()).padStart(2, '0');
      const seconds = String(now.getSeconds()).padStart(2, '0');
      
      // reqDate Start & End
      const dateStart = new Date(this.selectedStart);
      const newStart = dateStart.getFullYear() + '-' + String(dateStart.getMonth() + 1).padStart(2, '0') + '-' +  String(dateStart.getDate()).padStart(2, '0');
      const dateEnd = new Date(this.selectedEnd);
      const newEnd = dateEnd.getFullYear() + '-' + String(dateEnd.getMonth() + 1).padStart(2, '0') + '-' +  String(dateEnd.getDate()).padStart(2, '0');
      
      this.themeCopy.title = this.pilihanBSelected.nama;
      this.themeCopy.periode = newStart + ' s/d ' + newEnd;
      this.themeCopy.copyDate = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;

      if(this.themeCopyShow == true) {
        setTimeout(() => {
          this.captureImage();
        }, 2000);
      }
    },

    async captureImage() {
      const el = this.$refs.printcontent;

      const options = {
          type: "dataURL"
      };
      const printCanvas = await html2canvas(el, options);
      copyImageToClipboard(printCanvas.toDataURL("image/png"))
      .then(() => {
        this.$vs.loading.close();
        this.$swal.fire({
          title: 'Success',
          text: "Gambar berhasil di copy..",
              icon: 'success',
              timer: 1500
            })
            this.themeCopyShow = false 
            // console.log('Image Copied')
      })
      .catch((e) => {
          this.$swal.fire({
              title: 'Oopss !',
              text: e.message,
              icon: 'error',
              timer: 1500
          })
          // console.log('Error: ', e.message)
      })
    },

    updatePilihan(value) {

       this.$vs.loading()
         this.$http.get('toko/'+ value)
          .then(response => {
            if(response.data.status == "OK"){
              this.pilihanB = response.data.message
             }
             //else{
            //   this.$swal.fire(
            //     'Oopss !',
            //     response.data.message,
            //     'error'
            //   )
            // }
            })
          .catch(error => {error})
          setTimeout( ()=> {
            this.$vs.loading.close()
          }, 1000);
    },

    loadDataServer() {
        this.$vs.loading();
        this.cardShow = false;

        // DateNow CopyDate
        const now = new Date();
        const year = now.getFullYear();
        const month = String(now.getMonth() + 1).padStart(2, '0');
        const day = String(now.getDate()).padStart(2, '0');
        const hours = String(now.getHours()).padStart(2, '0');
        const minutes = String(now.getMinutes()).padStart(2, '0');
        const seconds = String(now.getSeconds()).padStart(2, '0');

        this.themeCopy.reqDate = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;

        var postData = {
          kode : this.pilihanBSelected.kode,
          nama : this.pilihanBSelected.nama,
          tgl_awal:  btoa(this.selectedStart),
          tgl_akhir: btoa(this.selectedEnd),
        }

        this.$http.post('pos/dashboardByDate', postData)
          .then(response =>{
            if(response.data.status =='SUKSES'){ 
              this.cardShow = true;
              // Grafik Chart Omzet  
              this.$refs.updateChart.updateSeries(
                response.data.message.nilai.slice(0,2)
              )
              this.$refs.updateChart.updateOptions({
                xaxis:{
                  categories: response.data.message.label
                }
              })
              this.$refs.updateChart.updateOptions({
                title:{
                  text: response.data.message.judul
                }
              })

              // Grafik Chart Session Omzet
              this.$refs.updateChartSession.updateSeries(
                response.data.message.session
              )
               this.$refs.updateChartSession.updateOptions({
                xaxis:{
                  categories:  response.data.message.labelsession
                }
              })
              this.$refs.updateChartSession.updateOptions({
                title:{
                  text: response.data.message.judul
                }
              })

              // Grafik Chart Sales
              this.$refs.updateCharthourlySales.updateSeries(
                response.data.message.serieshourlySales
              )
               this.$refs.updateCharthourlySales.updateOptions({
                xaxis:{
                  categories:  response.data.message.labelhourlysales
                }
              })
              this.$refs.updateCharthourlySales.updateOptions({
                title:{
                  text: 'Sales ' + response.data.message.judul
                }
              })

              // Grafik Chart Hourly Void
              this.$refs.updateCharthourlyVoid.updateSeries(
                response.data.message.serieshourlyVoid
              )
              this.$refs.updateCharthourlyVoid.updateOptions({
                xaxis:{
                  categories:  response.data.message.labelhourlyvoid
                }
              })
              this.$refs.updateCharthourlyVoid.updateOptions({
                title:{
                  text: 'Void ' + response.data.message.judul
                }
              })

              // Grafik Chart Date Void
              this.$refs.updateChartdateVoid.updateSeries(
                response.data.message.seriesdateVoid
              )
              this.$refs.updateChartdateVoid.updateOptions({
                xaxis:{
                  categories:  response.data.message.labeldatevoid
                }
              })
              this.$refs.updateChartdateVoid.updateOptions({
                title:{
                  text: 'Void ' + response.data.message.judul
                }
              })

              // Grafik Pie
              this.seriesPelanggan = [...response.data.message.seriesPelanggan]
              this.$refs.updateChartPelanggan.updateOptions({
                labels: response.data.message.labelPelanggan
              })

              // Board
              this.store = response.data.message.board.store
              this.noT = response.data.message.board.not
              this.salesTotal = response.data.message.board.sales
              this.averageNoTDay = response.data.message.board.avgNotDays
              this.averageSalesDay = response.data.message.board.avgSalesDays
              this.averageSalesTransaction = response.data.message.board.avgSalesTransaction
              this.averageNoTStoreDay = response.data.message.board.avgNotStore
              this.averageSalesStore = response.data.message.board.avgSalesStore
              this.cost = response.data.message.board.cost
              this.jumlah = response.data.message.kru.jumlah
              this.produktivitas = response.data.message.kru.produktivitas
              // Board2
              this.kontribusiPesanan = response.data.message.board2.kontribusiPesanan
              this.jmlPesanan = response.data.message.board2.jmlPesanan
              this.totalPelanggan = response.data.message.board2.totalPelanggan
              this.totalPesanan = response.data.message.board2.totalPesanan
              // Head Table
              this.bulan = response.data.message.headomzet.bulan
              this.namagerai = response.data.message.headomzet.namagerai
              this.targetgerai = response.data.message.headomzet.targetgerai
              // Table Omzet
              this.omzet = response.data.message.omzet.map(({ hari, tgl, notLMTD, notMTD, actualLMTD, actualMTD, cost, gerai, avgstr }) => ({ hari:(hari), tgl:parseInt(tgl), notLMTD: parseFloat(notLMTD.replace(/[^0-9.-]+/g,"")), notMTD: parseFloat(notMTD.replace(/[^0-9.-]+/g,"")), actualLMTD: parseFloat(actualLMTD.replace(/[^0-9.-]+/g,"")), actualMTD: parseFloat(actualMTD.replace(/[^0-9.-]+/g,"")), cost: parseFloat(cost.replace(/[^0-9.-]+/g,"")), gerai: parseInt(gerai), avgstr: parseFloat(avgstr.replace(/[^0-9.-]+/g,"")) }));
              // Table Sales Menu
              // this.salesmenu = response.data.message.salesmenu
              this.salesmenu = response.data.message.salesmenu.map(({ menu_toko, qty, rp, kontribusi_sales }) => ({ menu_toko:(menu_toko), qty: parseFloat(qty.replace(/[^0-9.-]+/g,"")), rp: parseFloat(rp.replace(/[^0-9.-]+/g,"")), kontribusi_sales: parseFloat(kontribusi_sales) }));
              // Judul Excel
              this.judul = response.data.message.judul
           }else{
            this.$swal.fire(
              'Gagal !',
              response.data.message,
              'error'
            )
           }
          })
          .catch(error => {error})
          .finally(() =>{this.$vs.loading.close()})
    },

    currency(value) {
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  }
}
</script>

